.sendMail{
    position: absolute;
    bottom: 0px;
    right: 22px;
    background-color: #404040;
    width: 40%;
    height: 50%;
    max-width: 500px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    display: flex;
    flex-direction: column;
    border: 1px solid whitesmoke;
    box-shadow: 0px 5px 7px 0px rgba(0,0,0,0.24);
}

.sendMail_header{
    display: flex;
    align-items: center;
    color: gray;
    padding: 13px;
    justify-content: space-between;
}

.sendMail_header > h3 {
    color: whitesmoke;
    font-size: 13px;
}

.sendMail_send{
    background-color: #3879ed !important;
    text-transform: capitalize !important;
    margin: 15px !important;
}

.sendMail_close{
    cursor: pointer;;
}

.sendMail > form{
    display: flex;
    flex: 1;
    flex-direction: column;
}

.sendMail > form > input {
    height: 30px;
    padding: 10px;
    border: none;
    border-bottom: 1px solid whitesmoke;
    outline: none;
}

.sendMail_Message{
    flex: 1;
}

.sendMail_error{
    background-color: white;
    color: red;
    text-align: right;
    /* padding: 2px; */
}