.section{
    display: flex;
    align-items: center;
    border-bottom-width: 2px;
    /* padding: 10px; */
    min-width: 200px;
    cursor: pointer;
    color: gray;
    border-width: 0 !important;
}

.section:hover{
    background-color: whitesmoke;
    border-width: 3px !important;
}

.section--selected{
    background-color: whitesmoke;
    border-left: 3px !important;
}

.section > h4 {
    font-size: 14px;
    margin-left: 15px;
}